import { Bool } from './bool'

export class AbstractFilter {

    public must(bool: Bool): this {
        bool.must(this);
        return this;
    }

    public filter(bool: Bool): this {
        bool.filter(this);
        return this;
    }

    public should(bool: Bool): this {
        bool.should(this);
        return this;
    }

    public mustNot(bool: Bool): this {
        bool.mustNot(this);
        return this;
    }
}